import React from 'react'
import { Route, Link, NavLink, BrowserRouter as Router, Switch, HashRouter } from 'react-router-dom';
import $ from 'jquery';

import Title from './Title';
import Home from './Home';
import Education from './Education';
import Skills from './Skills';
import Projects from './Projects';

class Nav extends React.Component {
    render() {
        return (

            <HashRouter basename="/nav/">
        <div>
        <div className="NavigationDiv">
            <ul>
                <li>
                
                    
                        <NavLink exact aciveClassName="active" to="/" className="HomeLink"></NavLink>
                    
                        <NavLink aciveClassName="active" to="/Aboutme">About me</NavLink>
                    
                        <NavLink aciveClassName="active" to="/Education">Education</NavLink>
                 
                   
                        <NavLink aciveClassName="active" to="/Skills">Skills</NavLink>
                        <NavLink aciveClassName="active" to="/Projects">Projects</NavLink>
                    
                
                   
                </li>
        
            </ul>
        </div>
            <Switch>
            <Route exact path="/" component={Title} />
                <Route path="/Aboutme" component={Home} />
                <Route path="/Education" component={Education} />
                <Route path="/Skills" component={Skills} />
                <Route path="/Projects" component={Projects} />
            </Switch>
        </div>
    </HashRouter>
            
        )
    }
}

export default Nav