import React from 'react'

const TextAlignCenter = {
    textAlign:'center'
};

const BorderBottom = {
    borderBottom:'1px solid white'
};

class Skills extends React.Component {
    render() {
        return (

            <div className="HomeContent">
            <br></br>
                <br></br>
               
                <div className="HeadStyle">
                    <div className="strip"><p>  </p></div>
                    <h4>Skills</h4>
                </div>
                <br></br>
                <div className="container">
                    <div className="HomeText">
                        <div className="HomeTextInner" style={TextAlignCenter}>
                   <div className="row">
                       <div className="col-sm">
                           <h3 style={BorderBottom}>Languages</h3>
                           <p>C++</p>
                           <p>Java</p>
                           <p>Python</p>
                           <p>JavaScript</p>
                           <p>PHP</p>
                       </div>

                       <div className="col-sm">
                           <h3 style={BorderBottom}>Development and Tools</h3>
                           <br></br>
                           <p>HTML</p>
                           <p>CSS</p>
                           <p>Android</p>
                           <p>ReactJS</p>
                           <p>MySQL</p>
                           <p>Firebase</p>
                           <p>Bootstrap</p>
                       </div>

                       <div className="col-sm">
                           <h3 style={BorderBottom}>Interested</h3>
                           <p>Databases</p>
                           <p>Web Technologies</p>
                           <p>DS and Algorithms</p>
                       </div>
                   </div>
                </div>
                </div>
                </div>


                <div className="container">
                    <div className="HomeText">
                        <div className="HomeTextInner">
                            <h4 style={TextAlignCenter}>Relevant Courses taken</h4>
                            <p>Introduction to Computer Science(Udacity), CS50(Harvard University), Introduction to Interactive Programming in Python (Coursera), Programming Foundations with JavaScript, HTML and CSS (Coursera), Algorithms Part 1 (Princeton University), Building Web Applications in PHP, Databases (Coursera), Android Basics(Google)
</p>
                    
                </div>
                </div>
                </div>
            </div>
            
        )
    }
}

export default Skills